.dialogs{
    grid-area: c;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: auto;
    grid-area: "s t t";
    gap: 30px;
}

.chat{
    width: 100%;
}

.senders_block{
    display: grid;
    grid-template-columns: 1fr;
    border-right: 1px solid #bebebe;
    padding-right: 5px;
}

.senders_block ul{
    list-style: none;
}

.senders_block ul li{
    margin-bottom: 5px;
    padding: 20px;
}

.senders_block ul li:hover{
    cursor: pointer;
    background-color: aliceblue;
}

.chat_history{
    width: 98%;
    height: 600px;
    background-color: #ebebeb;
    padding: 1%;
    overflow: hidden;
    overflow-y: auto;
}

.chat_history__owner {
    width: 45%;
    padding: 10px 4%;
    text-align: end;
    margin: 10px;
    border-radius: 5px;
    color: #fff;
    background-color: #5961aa;
    margin-left: auto;
}

.chat_history__interlocutor {
    width: 45%;
    padding: 10px 4%;
    text-align: start;
    margin: 10px;
    border-radius: 5px;
    background-color: #fff;
}

.type_message{
    display: grid;
    grid-template-columns: 7fr 1fr;
    grid-template-rows: 1fr;
}

.type_message textarea{
    resize: none;
}

.type_message button{
    color: #fff;
    border: 0;
    cursor: pointer;
    border-radius: 5px;
    background-color: #404f80;
}

.type_message button:hover {
    background-color: #2447b8;
    transition: 0.5s ease;
}

@media screen and (max-width: 600px) {
    .dialogs {
        grid-template-columns: 1fr 3fr;
        gap: 0;
    }
    .senders_block ul {
        padding: 0;
    }
}